<script lang="ts">
	import { page } from '$app/stores';
	import share from '$lib/images/share.svg';
  import CopyToClipboard from './CopyToClipboard.svelte';
  import { toast } from '@zerodevx/svelte-toast';
	import { binToBase64 } from '@bitauth/libauth';
	import { deflate } from 'pako';
	
  export let lockingBytecode: string;

  let linkText;

	if (lockingBytecode) {
		let q = decodeURI(binToBase64(deflate(lockingBytecode)));
		$page.url.searchParams.set('q', q);
    linkText = $page.url.host + "/s?" + $page.url.searchParams.toString();
	}

</script>

{#if lockingBytecode}
	<CopyToClipboard
		on:copy={() => toast.push('link copied')}
		text={linkText}
		let:copy
	>
		<div class="action">
			<button class="hitMe" on:click={copy}>
				<img src={share} alt="share" />
			</button>
		</div>
	</CopyToClipboard>
{/if}

<style>
	#progress-bar {
		max-width: 70px;
	}

	.hitMe {

		border: 0;
		line-height: 2.5;
		padding: 20px;
		font-size: 1rem;
		text-align: center;
		color: #fff;
		text-shadow: 1px 1px 1px #000;
		border-radius: 10px;
		background-color: rgb(178, 134, 207);
		background-image: linear-gradient(
			to top left,
			rgba(0, 0, 0, 0.2),
			rgba(0, 0, 0, 0.2) 30%,
			rgba(0, 0, 0, 0)
		);
		box-shadow: inset 2px 2px 3px rgba(255, 255, 255, 0.6), inset -2px -2px 3px rgba(0, 0, 0, 0.6);
	}

	@keyframes blinker {
		50% {
			opacity: 0;
		}
	}
</style>
